import { useCommentIds, useComments } from "../hooks/comments";
import { Comment } from "./Comment";

import { CSSTransition, SwitchTransition } from "react-transition-group";

import s from "../style/commentSection.module.sass";

export function CommentSection(props) {
    //load comments
    var comIds = useCommentIds(props.open, props.id);
    var comments = useComments(comIds);

    //required by css switch transition
    var animKey = undefined;

    var comObjects = comments.map((comment) => {
        //comments not yet loaded
        if(comment === "not epic") {
            animKey="wait";
            return <p>Loading...</p>;
        } 
        //COMMENTS!
        animKey="cs";
        return (<Comment key={comment.entityId} comment={comment}/>)
    });
    //no comments :(
    if (!comments[0]) {
        animKey = "no";
        comObjects = [<p>There are no Comments</p>];
    }
    if(!props.open) {
        animKey="hurensohn nein";
        comObjects = [<></>];
    }

    function calculateHeight (el, appear) {
        var height = el.offsetHeight;

        if(appear) {
            return setTimeout(this.calculateHeight(el), 100)
        }

        this.setState(prev => {
            if(!prev.backing) {
                height+=16;
            }
            return {menuHeight: height}
        });
    }


    return (
        <div className={s.commentSectionWrap}>
            <SwitchTransition mode={"out-in"}>
                <CSSTransition 
                    timeout={1000} 
                    classNames={{ ...s }} 
                    component="div"
                    key={animKey}
                >     
                    {props.open? <div className={s.commentSection}>{comObjects}</div> : <></>}
                </CSSTransition>
            </SwitchTransition>
        </div>
    );
}
